.work-order-list-cont {
	display: flex;
	flex-direction: column;
	gap: 10px;
	height: 90%;
	overflow-y: scroll;
}
.work-order-list-cont>.wo-preview-card {
	min-width: 100%;
}
.work-order-list-cont.completed>.wo-preview-card {
	height: 15% !important;
	min-height: 165px;
}
/*Make them flex*/
.work-order-list-cont>.wo-preview-card>.card-content {
	display: grid;
	grid-template-columns: 85% 15%;
	height: 100%;
}
.work-order-list-cont>.wo-preview-card>.card-content>.action-row {
	display: flex;
	flex-direction: column;
	min-height: 100%;
	padding: 10px;
}
.work-order-list-cont>.wo-preview-card>.card-content>.action-row>.action-button {
	width: 75%;
	height: fit-content;
	padding: 5px;
}
.work-order-list-cont>.wo-preview-card>.card-content>.work-order-summary-cont {
	display: grid;
	grid-template-columns: 20% 79%;
	gap: 1%;
	min-height: 100% !important;
	max-height: 100%;
	border-bottom: 0px;
}
.work-order-list-cont>.wo-preview-card>.card-content>.work-order-summary-cont>.wo-prev-info-cont {
	display: flex;
	flex-direction: column;
}
.work-order-list-cont>.wo-preview-card>.card-content>.work-order-summary-cont>.wo-prev-info-cont>.wo-prev-date {
	font-size: 30px;
}
.work-order-list-cont>.wo-preview-card>.card-content>.work-order-summary-cont>.wo-prev-info-cont>.wo-prev-wonum {
	font-size: 20px;
}
.work-order-list-cont>.wo-preview-card>.card-content>.work-order-summary-cont>.wo-prev-info-cont>.warning {
	height: 15%;
}
.work-order-list-cont>.wo-preview-card>.card-content>.work-order-summary-cont>.wo-descr {
	max-height: 100%;
	font-size: larger;
}

#wo-search-input, #wo-search-button {
	height: 40px;
	line-height: 40px;
}
#wo-search-input {
	width: 300px;
}

#wo-search-button {
	background-color: #0e4474;
	font-weight: 650;
	font-size: large;
}
#wo-search-button:hover {
	background-color: #062a49;
}


.submit-spiff-wo-button {
	background-color: #444f74;
}
.submit-spiff-wo-button:hover {
	background-color: #333c58;
}
.open-wo-button {
	background-color: #56a775;
}
.open-wo-button:hover {
	background-color: #45855d;
}
.remove-wo-button {
	background-color: #a75656;
}
.remove-wo-button:hover {
	background-color: #854545;
}
.view-summary-wo-button {
	background-color: #9756a7;
}
.view-summary-wo-button:hover {
	background-color: #734180;
}

@media screen and (orientation: portrait) {
	/*View customizations*/
	.work-order-list-cont>.wo-preview-card>.card-content {
		grid-template-columns: 75% 25%;
	}
	.work-order-list-cont>.wo-preview-card>.card-content>.work-order-summary-cont {
		display: grid;
		grid-template-columns: 40% 60%;
	}
	.work-order-list-cont {
		height: 92.5%;
	}
}

/*Surface Go: 200% scaling, 100% browser zoom*/
@media screen and (min-width: 924px) and (max-width: 1024px) {
	.work-order-list-cont {
		height: 87.5%;
	}
	.work-order-list-cont>.wo-preview-card>.card-content>.work-order-summary-cont {
		display: grid;
		grid-template-columns: 30% 69%;
		gap: 1%;
	}
}

/*Surface Go: 200% scaling, 100% browser zoom*/
@media screen and (orientation: portrait) and (min-width: 924px) and (max-width: 1024px) {
	.work-order-list-cont {
		height: 57.5% !important;
		background-color: #45855d !important;
	}
}