/*CHECKLIST MOCK*/
/*Container*/
.checklists-list {
	padding-top: 10px;
	height: calc(100vh - 125px);
	width: 100%;
	overflow-y: overlay;
	padding-bottom: 10px;
}
.checklists-list#list {
	display: flex;
	flex-direction: column;
	row-gap: 2.5%;
}
.checklists-list#medium-cards {
	display: grid !important;
	grid-template-columns: 49.5% 49.5% !important;
	column-gap: 0;
	row-gap: 10px;
	gap: 1%;
	overflow-y: overlay !important;
}
/*Cards*/
.checklist-card {
	min-width: 100%;
	min-height: fit-content;
}
.checklists-list#medium-cards>.checklist-card {
	min-height: calc(100vh - 140px);
	max-height: calc(100vh - 140px);
}
.checklist-card>.card-content {
	height: calc(100% - 40px);
	overflow-y: overlay;
}
.checklist-card>.card-menubar {
	position: relative;
	background-color: #5c1d1d;
	overflow: hidden;
}
.checklist-card.cl-green>.card-menubar {
	background-color: #1d5c25;
}
.checklist-card.cl-purple>.card-menubar {
	background-color: #3c1a52;
}
.checklist-card.cl-orange>.card-menubar {
	background-color: #d37116;
}
.checklist-card.cl-blue>.card-menubar {
	background-color: #1662d3;
}
.checklist-card.cl-red>.card-menubar {
	background-color: #5c1d1d !important;
}
.checklist-card.cl-lightred>.card-menubar {
	background-color: #ac596b !important;
}
.checklist-card.cl-yellow>.card-menubar {
	background-color: #acab59 !important;
}
.checklist-card.cl-brown>.card-menubar {
	background-color: #3a1b1e;
}
.checklist-card.cl-aquamarine>.card-menubar {
	background-color: #3c7c67;
}
.checklist-card.cl-yellow>.card-menubar>.card-title {
	color: #212121 !important;
}

/*Checklist minimized*/
.checklist-card#minimized {
	min-height: unset !important;
	max-height: unset !important;
	height: fit-content;
}
.checklist-card#minimized>.card-content {
	display: none !important;
}
.checklist-card#minimized>.card-menubar {
	border-radius: 10px;
	overflow: hidden;
}
.checklist-card>.card-menubar>.card-actions>.minimize-button {
	right: 0;
	background-color: #0000003f;
	width: 80px;
}
.checklist-card>.card-menubar>.card-actions>.minimize-button:hover {
	background-color: #00000081;
}

/*Filters*/
.checklist-chip {
	line-height: unset;
	overflow: hidden;
   display: -webkit-box;
   -webkit-line-clamp: 1; /* number of lines to show */
           line-clamp: 1; 
   -webkit-box-orient: vertical;
}
/*CHIP COLORS*/
.filter-button {
	background-color: #116dbd !important;
	border: 1px solid #0b4e8a !important;
	color: #fff !important;
}
.filter-button:hover {
	background-color: #0b4e8a !important;
}
.filter-button.selected {
	background-color: #0b4e8a !important;
}
.filter-button.selected>img {
	filter: invert();
}
