#checklist-chips-row {
	margin-bottom: 10px;
	overflow-x: scroll;
	overflow-y: hidden;
	/*flex-wrap: wrap;*/
}
#checklist-top-actions-cont {
	padding: 10px;
}

@media screen and (orientation:portrait) {
	/*View customizations*/
	.checklists-list#list>.card>.card-content>.card-content {
		display: flex;
	}
	.checklists-list#list>.card>.card-content>.card-content>:nth-child(2n + 1) {
		padding-right:  0%;
		border-right: 0px solid #ccc;
	}
	/*disallow side by side in portrait mode*/
	.checklists-list#medium-cards {
		display: flex !important;
		height: calc(100vh - 117px);
		flex-direction: column !important;
		column-gap: 0;
		row-gap: 10px;
		gap: 1%;
		overflow-y: scroll !important;
	}
	.checklists-list#medium-cards>.checklist-card {
		max-height: unset;
		min-height: fit-content !important;
		width: 100% !important;
	}
}

/*Surface Go: 200% scaling, 100% browser zoom*/
@media screen and (min-width: 924px) and (max-width: 1024px) {
	.checklist-chip {
		font-size: small
	}
}