/*Warnings*/
.warning {
	height: 100%;
	display: flex;
	align-items: center;
	gap: 5px;
}
.warning>img {
	height: 15px;
	filter:invert(30%) sepia(41%) saturate(7477%) hue-rotate(337deg) brightness(99%) contrast(90%);
}
.warning>.warning-text {
	font-style: italic;
	font-weight: 600;
	color: #f0314b;
}

.warning.purple>.warning-text {
	color: #9951c9;
}
.warning.purple>img {
	filter: invert(58%) sepia(53%) saturate(6609%) hue-rotate(246deg) brightness(83%) contrast(88%);
}

.warning.blue-green>.warning-text {
	color: #51c9b9;
}
.warning.blue-green>img {
	filter: invert(73%) sepia(60%) saturate(383%) hue-rotate(119deg) brightness(89%) contrast(84%);
}

.warning.blue>.warning-text {
	color: #5181c9;
}
.warning.blue>img {
	filter: invert(58%) sepia(7%) saturate(4053%) hue-rotate(180deg) brightness(82%) contrast(89%);  
}
.warning.white>.warning-text {
	color: #fff;
}
.warning.white>img {
	filter: invert()
}