/*Scoring*/
.bar-chart-bar {
	background-color: #1973c2;
	border-top: 2px solid #07355e;
	border-bottom: 2px solid #07355e;
	border-right: 2px solid #07355e;
}
.bar-chart-bar-label {
	color: #fff;
}

/*PRESENTATION: SCORING*/
.score-text-cont {
	padding: 10px;
	padding-top: 0;
}

.score-text,
.score-grade {
	text-align: left;
	font-size: large;
	font-weight: 600;
	padding-top: 5px;
	padding-bottom: 5px;
}

.grade-descr {
	text-align: left;
	font-size: x-large;
}

.grade-descr>span {
	font-weight: 600;
}
/*@todo adjust so it pulls color based on grade - key code them with numbers?*/
.score-actual {
	padding: 5px;
	border-radius: 5px;
}
.guage-cont {
	display: flex;
	gap: 20px;
	width: 100%;
	height: 130px;
}

#guage-chart {
	justify-self: start;
	width: 130px;
	height: 100px;
}

/*Checklist Scoring*/
#ChecklistSummaryCard>.card-menubar {
	position: relative;
}
#ChecklistSummaryCard-card-content {
	height: calc(100% - 40px);
	overflow-y: scroll;
}
.grade-title-cont {
	display: grid;
	grid-template-columns: 80% 20%;
	font-size: 20px;
	justify-items: left;
	margin-top: 10px;
	margin-bottom: 10px;
}
.grade-score {
	justify-self: end;
	padding: 10px;
	border-radius: 10px;
	font-weight: 600;
}
.grade-score::after {
	content: '%';
}
.grade-title {
	padding: 10px;
	border-radius: 10px;
	font-weight: 600;
}
.grade-title.Excellent,
.grade-score.Excellent,
.score-actual.Excellent {
	background-color: #00c602;
    border: 1px solid #007901;
}
.grade-title.Good,
.grade-score.Good,
.score-actual.Good {
	background-color: rgb(255, 247, 0);
    border: 1px solid rgb(178, 172, 0);
}
.grade-title.Average,
.grade-score.Average,
.score-actual.Average {
	background-color: rgb(255, 187, 0);
    border: 1px solid rgb(165, 121, 0);
}
.grade-title.Consider.Replacement,
.grade-score.Consider.Replacement,
.score-actual.Consider.Replacement {
	background-color: rgb(255, 136, 0);
    border: 1px solid rgb(155, 83, 0);
}
.grade-title.Replacement.Recommended,
.grade-score.Replacement.Recommended,
.score-actual.Replacement.Recommended {
	background-color: rgb(218, 60, 60);
    border: 1px solid rgb(178, 0, 0);
}
/*Checklist summary header*/
.checklist-summary-list>.table-row.header {
	display: none;
}
.checklist-summary-list>.tablerow-container {
	height: max-content;
	border: 1px solid #ccc;
	background-color: transparent;
	border-radius: 10px;
	padding: 0px;
	/*display: grid;
	grid-template-columns: repeat(2, 50%);*/
	display: flex;
	flex-direction: column;
	font-size: larger;
	align-content: center;
    justify-content: center;
	overflow: hidden;
}
.checklist-summary-list>.tablerow-container>.table-row {
	border-bottom: 1px solid #ccc;
	border-right: 0px solid #ccc;
	grid-template-columns: 40% 40% 20%;
	padding: 0px !important;
	width: 100%;
	background-color: transparent;
	height: fit-content;
	/*display: grid;
    grid-template-columns: 37.5% 32.5% 22.5%;
    gap: 0.5%;
    padding: 5px;
    border-top: 1px solid #ccc;
    background-color: transparent;
    align-items: center;
    justify-content: center;
	height: 100%;*/
}
.checklist-summary-list>.tablerow-container>.table-row>div {
	background-color: transparent !important;
	padding: 10px;
	margin: 0px !important;
}
.checklist-summary-list>.tablerow-container>:first-child,
.checklist-summary-list>.tablerow-container>:nth-child(2) {
	border-top: 0px solid #ccc;
}
.checklist-summary-list>.tablerow-container>:nth-child(2n + 1) {
	border-right: 0px solid #ccc;
}
.checklist-summary-list>.tablerow-container>:last-child {
	border-bottom: 0px;
}
/*PRESENTATION*/
.score-card-container {
	width: 99.5%;
	display: grid;
	grid-template-columns: 56% 43%;
	height: calc(100vh - 80px);
	gap: 1%;
}
.score-card-container#system-score {
	display: grid;
	grid-template-columns: 43% 56%;
}

.summary-title-header {
	font-size: 40px;
    text-align: center;
    padding-top: 5px;
    padding-bottom: 5px;
    margin-bottom: 10px;
    background-color: rgb(15 70 119);
    color: #fff;
    width: 100%;
    border-radius: 10px;
}

.summary-header>.action-button {
	height: 45px;
	font-size: 30px;
	line-height: 40px;
	width: 100px;
	margin-right: 20px;
	background-color: rgb(15 70 119);
}
.summary-header>div {
	text-align: center;
}
#cl-start-over-button {
	font-size: unset;
	line-height: unset;
	font-size: larger;
	width: 70px;
	margin-right: 5px;
}
.summary-header>.action-button:hover {
	background-color: rgb(10, 54, 92);
}

#SystemScoreSummaryCard>.card-menubar {
	position: relative;
}

/*Surface Go: 200% scaling, 100% browser zoom*/
@media screen and (min-width: 924px) and (max-width: 1024px) {
	.score-text,.score-grade {
		font-size: x-small;
		width: 100%;
	}
	#SystemScoreSummaryCard>.card-content {
		height: calc(100% - 40px);
		overflow-y: scroll;
		overflow-x: hidden;
	}
}

/*Portrait Mode*/
@media screen and (orientation:portrait) {
	.score-card-container#system-score {
		display: grid;
		grid-template-columns: unset;
		grid-template-rows: 57% 43%;
		gap: 1%;
	}

	.pres-left-full {
		min-width: 100%;
	}
	.pres-left-full>.card-content {
		height: calc(100% - 40px);
		overflow-y: scroll;
	}
	.summary-title-header {
		height: 40px;
		margin: 0px;
		padding: 0px;
	}
	.summary-header>.action-button {
		height: 40px;
	}
	.summary-title-header>.header-text {
		font-size: 17.5px !important;
		line-height: 40px;
	}
}