/*SERVICE ITEMS DETAILS CARD*/
#service-items-detail-card {
	min-width: 70vw;
	max-width: 70vw;
	height: 80vh;
}
#service-items-detail-card-card-content {
	display: grid;
	grid-template-columns: 50% 50%;
	overflow-y: overlay;
	height: calc(80vh - 40px);
}
.service-items-detail-form {
	display: flex;
	flex-direction: column;
	row-gap: 10px;
	font-size: 20px;
}
.service-items-detail-form>.form-info-item {
	display: grid;
	grid-template-columns: 50% 50%;
	align-items: center;
}
.service-items-detail-form>.form-info-item>.form-info-title {
	font-size: 21px;
}
.service-items-detail-form>.form-info-item>input {
	width: 100%;
	padding: 10px;
	background-color: #fafafa;
}
.service-items-detail-form>.form-info-item>input:hover {
	background-color: #ececec;
}
.service-items-detail-form>.form-info-item>input:focus {
	outline: none;
    background-color: #ccc;
    color: rgb(25,25,25);
}