/*Service items View settings*/
.service-items-list {
	padding-top: 10px;
	height: calc(100vh - 66px);
	width: 100%;
	overflow-y: scroll;
	padding-bottom: 10px;
}
.service-items-list.tworows {
	padding-top: 10px;
	height: calc(100vh - 60px);
	width: 100%;
	overflow-y: scroll;
	padding-bottom: 10px;
}
.service-items-list#list {
	display: flex;
	flex-direction: column;
	row-gap: 2.5%;
	border-radius: 10px;
}
.service-items-list#medium-cards {
	display: grid !important;
	grid-template-columns: 50% 50% !important;
	column-gap: 0;
	row-gap: 20px;
}
/*Repair Page*/
#add-new-serviceitem, #reset-si-filters {
	background-color: #0b4e8a;
	font-size: 20px;
	overflow: hidden;
}
#add-new-serviceitem:hover, #reset-si-filters:hover {
	background-color: #07355e;
}
#reset-si-filters {
	width: fit-content;
	padding-left: 10px;
	padding-right: 10px;
}
.sort-button {
	background-color: transparent;
	width: fit-content;
	padding: 5px;
	filter: unset;
	height: 35px;
}
.sort-button:hover {
	background-color: #ccc;
}
.sort-button>img {
	padding: 0;
	height: 25px;
	width: 25px;
	filter: brightness(0) saturate(100%) invert(3%) sepia(7%) saturate(29%) hue-rotate(314deg) brightness(99%) contrast(79%);
}
.sort-button>img:hover {
	background-color: #ccc;
	filter: brightness(0) saturate(100%) invert(3%) sepia(7%) saturate(29%) hue-rotate(314deg) brightness(99%) contrast(79%);
}
.sort-button.selected {
	background-color: #ccc;
}
.sort-button.selected>img {
	filter: brightness(0) saturate(100%) invert(3%) sepia(7%) saturate(29%) hue-rotate(314deg) brightness(99%) contrast(79%);
}

#repairs-top-actions-cont {
	align-items: center;
}
#repairs-top-actions-cont>.filter-row {
	overflow-y: hidden;
	max-width: 65.5%;
}

.sort-drop-cont {
	width: 200px;
	align-items: center;
}
.sort-drop-cont>.form-info-title {
	font-size: 20px;
	text-align: center;
	color: #424242;
	font-weight: bolder;
	width: 100%;
}

/*SERVICE ITEMS*/
/*Repair Table*/
.si-repairs>.datatable>.tablerow {
	display: grid;
	grid-template-columns: 10% 20% 30% 20% 20%;
}
.si-remove-repair-button:hover {
	background-color: #ccc;
}
.si-remove-repair-button {
	background-color: transparent;
	width: 35px;
	height: 35px;
	filter: unset;
}
.si-remove-repair-button>img {
	margin-top: 7.5px;
	height: 20px;
	width: 20px;
}

.repair-table {
	min-width: 100%;
	max-width: 100%;
}

.repair-table>.card-content {
	padding: 0px;
}

#title-row {
	padding: 5px;
}
#title-row>input {
	padding: 8px;
	background-color: #fafafa;
}
#title-row>input:hover {
	background-color: #ececec;
}
#title-row>input:focus {
	outline: none;
    background-color: #ccc;
    color: rgb(25,25,25);
}
#title-row>.action-button {
	height: 35px;
	width: 35px;
	font-size: 30px;
	background-color: #0b4e8a;
}
#title-row>.action-button:hover {
	background-color: #07355e;
}

.si-card, .repair-table {
	filter: unset;
	border: 1px solid #ccc;
}

.repair-table {
	overflow-y: overlay;
}

.si-table {
	border-bottom: 1px solid #e2e2e2;
	display: grid;
	grid-template-columns: 15% 12.5% 54.5% 10.5% 2%;
	padding: 0px;
	align-items: center;
	height: max-content;
	height: fit-content;
}
.si-table.other-repair {
	grid-template-columns: 15% 12.5% 44.5% 10% 10.5% 2%;
}
.si-tablerow-price::before {
	content: '$'
}
.si-table:hover {
	background-color: #e2e2e2;
}

#add-item {
	width: max-content;
	padding-left: 10px;
	padding-right: 10px;
}

.si-tag {
	font-size: 20px;
	font-weight: 550;
	font-style: italic;
	color: #212121;
}
.si-id {
	font-size: 17.5px;
	color: #646464;
}

.repair-checkboxes {
	border: 1px solid #ccc;
	padding: 10px;
	border-radius: 10px;
	display: flex;
	flex-direction: column;
	row-gap: 10px
}
.repair-checkboxes>.form-info-item {
	display: grid;
	grid-template-columns: 10% 85%;
	align-items: center;
}
.repair-checkboxes>.form-info-item>.form-info-title {
	text-align: left;
	text-indent: 5px;
	width: 100%;
}

.repair-checkboxes.repair-table-checks {
	display: flex;
	flex-direction: row;
	width: 100%;
	border: 0px;
}
.repair-checkboxes.repair-table-checks>.form-info-item {
	width: 100%;
	gap: 0px;
}
.repair-checkboxes.repair-table-checks>.form-info-item>.form-info-title {
	font-weight: 560 !important;
	order: 1;
}
.repair-checkboxes.repair-table-checks>.form-info-item>.checkbox-cont {
	border: 1px solid #ccc !important;
	background-color: #f1f1f1;
}
.repair-checkboxes.repair-table-checks>.form-info-item>.checkbox-cont:hover {
	background-color: #ccc;
}

.si-actions {
	align-items: center;
}

@media screen and (orientation:portrait) {
	#repairs-top-actions-cont {
		flex-direction: column-reverse;
		align-items: start;
	}
	#repairs-top-actions-cont>.filter-row {
		max-width: 100%;
	}
	.service-items-list#medium-cards {
		display: flex !important;
		flex-direction: column;
		grid-template-columns: 50% 50% !important;
		column-gap: 0;
		row-gap: 10px;
	}
}

/*Surface Go: 200% scaling, 100% browser zoom*/
@media screen and (min-width: 924px) and (max-width: 1024px) {
	.si-repairs {
		padding: 5px;
	}
	#repairs-top-actions-cont>.filter-row {
		overflow-y: hidden;
		max-width: 55.5%;
	}
	.si-tablerow-appr {
		font-size: 15px;
	}
	.si-tablerow-descr {
		font-size: 14px;
		height: fit-content;
	}
	.si-tablerow-task {
		font-size: 14px;
	}
	.si-table {
		width: 100%;
		grid-template-columns: 15% 16% 44% 12.5% 2% !important;
	}
	.si-table.other-repair {
		grid-template-columns: 18% 8% 39% 10% 12.5% 2% !important;
	}
}