/*Repairs Summary*/
.repair-sitem-header {
	font-style: italic;
}
.repair-sitem-header>div {
	color: #212121;
	text-indent: 10px;
	font-weight: 400;
	font-size: 17.5px;
}
#RepairSummaryCard>.card-menubar {
	position: relative;
}
#RepairSummaryCard>.card-content {
	padding: 0px;
	height: calc(100% - 40px);
}
#RepairSummaryCard>.card-content>.card-content {
	height: calc(69% - 27.5px);
	overflow-y: overlay;
	scrollbar-gutter: stable;
}
#RepairSummaryCard>.card-content>.invoice-total-cont {
	height: fit-content;
	border-bottom: 1px solid #ccc;
	overflow: hidden;
	background-color: #fafafa;
}
.pres-repair-price-row {
	display: grid;
	grid-template-columns: 40% 12.5% 17.5% 12.5% 17.5%;
	/*padding-top: 10px;
	padding-bottom: 10px;*/
	font-size: 22.5px;
}
.pres-repair-header-row {
	display: grid;
	grid-template-columns: 52.5% 17.5% 12.5% 17.5%;
}
.pres-repair-header-row>.highlight {
	background-color: #86ff8c;
	min-width: 100%;
	height: 100%;
}
.pres-repair-price-row:hover {
	background-color: #f0f0f0;
}
.pres-repair-price-row.NO>.si-tablerow-price,
.pres-repair-price-row.NO>.si-tablerow-task,
.pres-repair-price-row.PAID>.si-tablerow-price,
.pres-repair-price-row.PAID>.si-tablerow-task {
	font-style: italic;
	color: #212121;
	font-weight: 300;
}
.pres-repair-price-row>div {
	padding: 10px;
	height: 100%;
}
.pres-repair-price-row.NO>.si-tablerow-task,
.pres-repair-price-row.NO>.si-tablerow-price {
	text-decoration: line-through;
}
.si-tablerow-appr {
	background-color: transparent;
	filter: none;
	width: 100%;
	font-weight: 600;
	font-size: 20px;
}
.si-tablerow-appr:hover {
	background-color: #ccc;
}
.si-tablerow-appr#YES,.si-tablerow-appr[id = 'RETURN FOR'] {
	color: #3452a0;
}
.si-tablerow-appr[id = 'ALREADY PAID'],
.si-tablerow-appr[id = 'PAID']  {
	color: #34a073;
}
.si-tablerow-appr[id = 'RETURN FOR'] {
	
}
.si-tablerow-task {
	font-size: 18px;
}

.si-tablerow-appr#NO {
	color: #e60000;
}

.si-tablerow-price.mprice,
#mem-due-today,
#monthly-mem>.price,
#msavings {
	background-color: #86ff8c;/*#f0f0f0*/;
	font-weight: 600;
	color: #000;/*rgb(54, 145, 248)*/;
	/*animation-name: textshine;
    animation-duration: 8s;
    animation-iteration-count: infinite;
    animation-direction: alternate-reverse;*/
}
#monthly-mem>.price {
}
#pres-labels {
	border-bottom: 1px solid #ccc;
	background-color: #f0f0f0;
	font-weight: 600;
	font-size: 17.5px;
	align-items: center;
	justify-content: center;
	height: 50px;
	grid-template-columns: calc(40% - 8px) 12.5% calc(17.5% - 2px) 12.5% 17.5%;
}
#pres-labels>div {
	height: 100%;
	padding: 0px;
	line-height: 50px;
}
.repair-price-cont {
	
}
/*Animations*/
@keyframes textshine {
    0%   {color: rgb(255, 63, 63);}
    25%  {color: rgb(47, 162, 255);}
    50%  {color: rgb(255, 63, 63);}
    100% {color: rgb(47, 162, 255);}
}

/*Surface Go: 200% scaling, 100% browser zoom*/
@media screen and (min-width: 924px) and (max-width: 1024px) {
	#RepairSummaryCard>.card-content {
		display: flex;
		flex-direction: column;
	}
	#RepairSummaryCard>.card-content>:nth-child(3) {
		height: calc(60% - 27.5px);
		overflow-y: overlay;
		scrollbar-gutter: stable;
	}
}