body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
	  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
	  sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.reset {
	position: absolute !important;
	top: 0px !important;
	left: 0px !important;
	bottom: 0px !important;
	right: 0px !important;
}

#main-dash>.side-bar-menu-cont>.menu-bar>:nth-child(1)>img {
	filter: invert();
}
#main-dash>.side-bar-menu-cont>.menu-bar>:nth-child(3)>img {
	filter: invert();
}
  
  code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
	  monospace;
  }
  
  #invert-button>img {
	  filter: invert();
  }
  
  /*Cards*/
  .minimize-button {
	  position: absolute;
	  line-height: 5px;
	  padding: 17.5px;
	  right: 43.5px;
	  background-color: #062a49;
	  color: var(--card-base-light);
	  font-weight: 500;
  }
  .minimize-button:hover {
	  background-color: #041c31;
  }
  
  /*Float Container*/
  .float-container.minimized {
	  background-color: transparent;
	  transition: 0.75s;
	  pointer-events: none;
  }
  .float-container.minimized>.card {
	  transition: 0.75s;
	  min-height: 40px !important;
	  max-height: 40px !important;
	  overflow: hidden;
	  pointer-events:all;
  }
  .float-container.minimized>.card>.card-content {
	  display: none;
  }
  
  
  /*Base classes*/
  
  .outline-card {
	  border: 1px solid #e2e2e2;
	  filter: unset;
  }
  
  .no-padding {
	  padding: 0;
  }
  
  .multi-form-item {
	  display: flex;
	  gap: 5px;
  }
  
  .two-thirds-top-column {
	  display: grid;
	  grid-template-rows: 66% 32%;
	  gap: 2%;
	  max-height: 100%;
	  min-height: 100%;
  }
  .four-fifths-top-column {
	  display: grid;
	  grid-template-rows: 84% 14%;
	  gap: 2%;
	  max-height: 100%;
	  min-height: 100%;
  }
  
  .one-fifth-top-column {
	  display: grid;
	  grid-template-rows: 18% 80%;
	  gap: 2%;
	  max-height: 100%;
	  min-height: 100%;
  }
  
  
  #membership-options-card {
	  max-height: 100%;
	  min-height: 100%;
	  font-size: smaller !important;
  }
  
  /*Creator Form Base Class*/
  .creator-form {
	  display: flex;
	  flex-direction: column;
	  gap: 10px;
  }
  .creator-form>.form-info-item {
	  align-content: center;
	  display: grid;
	  grid-template-columns: 40% 60%;
  }
  .creator-form>.form-info-item>.form-info-title {
	  font-size: x-large;
	  height: 100%;
  }
  .creator-form>.form-info-item>input {
	  padding: 10px;
	  width: 100%;
  }
  .creator-form>.checkbox-form {
	  display: flex;
	  flex-direction: column;
	  padding: 0px;
  }
  .creator-form>.checkbox-form>.form-info-item {
	  width: 100%;
	  row-gap: 10px;
	  padding: 10px;
	  display: grid;
	  grid-template-columns: 90% 10%;
  }
  .creator-form>.checkbox-form>.form-info-item>.form-info-title {
	  font-size: 20px;
  }
  
  .more-info-button,.Text-Only-Button,.text-only-button {
	  width: max-content;
	  background-color: transparent;
	  filter: unset;
	  color: #c94a4c;
	  cursor: pointer;
	  font-weight: 650;
  }
  
  .more-info-button:hover,.Text-Only-Button:hover,.text-only-button:hover {
	  background-color: transparent;
	  color: #912d2f;
  }
  
  
  .pres-left-full {
	  min-height: 100% !important;
	  max-height: 100% !important;
  }
  
  @media screen and (orientation:portrait) {
	  /*View customizations*/
	  .checklists-list#list>.card>.card-content>.card-content {
		  display: flex;
		  flex-direction: column;
	  }
	  .checklists-list#list>.card>.card-content>.card-content>:nth-child(2n + 1) {
		  padding-right: 0%;
		  border-right: 0px solid #ccc;
	  }
	  .checklists-list#medium-cards {
		  display: flex !important;
		  flex-direction: column;
	  }
	  .checklists-list#medium-cards>.checklist-card {
		  min-height: calc(56.5vh - 150px);
		  max-height: calc(56.5vh - 150px);
	  }
}




/*Text input warning*/

.input-cont {
	background-color: #cecece;
	border-radius: 5px;
	height: 12px;
}

.input-cont>.warning-cont {
	border-bottom-left-radius: 5px;
	border-bottom-right-radius: 5px;
}

.input-cont>.warning-cont>.warning {
	font-size: x-small;
	padding-left: 5px;
	padding-right: 5px;
}