.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.tablerow>div {
	margin: 0px;
	height: fit-content;
}

.outline-card {
	border: 1px solid #ccc;
	filter: unset;
}

.no-padding {
	padding: 0px;
}

.image-button-icon {
	width: 25px;
	height: 25px;
}

.menu-bar {
	display: flex;
	flex-direction: column;
	gap: 10px;
}
.menu-bar.top-bar {
	flex-direction: row;
	gap: 0px;
	filter: unset;
	width: 100%;
}

.menu-button {
	display: flex;
	background-color: transparent;
	height: 40px;
	line-height: unset;
	font-size: 20px;
}

.collapsed>div>div {
	color: transparent
}

.side-bar.collapsed {
	width: 65px !important;
}
.side-bar-menu-cont.collapsed {
	width: 65px !important ;
}
.side-bar-menu-cont {
	width: 160px;
	transition: 0.25s;
}

.side-bar-container {
	position: absolute;
    left: 180px;
    height: calc(100vh - 20px);
    width: calc(100vw - 180px);
    overflow-y: overlay;
    top: 20px;
	transition: 0.25s;
}

.side-bar-menu-cont.collapsed>.side-bar-bottom-buttons>.action-button,
.side-bar.collapsed>.menu-button {
	width: 40px !important;
	height: 40px;
}
.side-bar>.menu-button {
	width: 145px;
	margin-top: 10px;
	line-height: 40px;
}
.menu-button {
	border-radius: 5px;
}
.menu-button>img {
	margin-left: 6px;
	margin-top: 6px;
	margin-right: 4.5px;
}

.side-bar-bottom-buttons>.action-button>img {
	height: 25px !important;
	width: 25px !important;
}

.side-bar-menu-cont>.side-bar-bottom-buttons>.action-button {
	width: 145px !important;
	font-size: 25px;
	text-indent: 3px;
	line-height: 40px;
	height: 40px;
}

.action-button.side-bar-menu {
	height: 40px;
	width: 40px;
}
.action-button.side-bar-menu>img {
	height: 20px;
	width: 20px;
}

