.vhc-load-screen-item-cont {
	border-radius: 5px !important;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap: 50px;
	width: 100%;
}

.vhc-load-screen-text {
	width: 600px;
}

.vhc-load-screen-img {
	background-color: #61dafb;
}

/*LOGIN SCREEN 200% SCALING SURFACE GO*/
/*Surface Go: 200% scaling, 100% browser zoom*/
@media screen and (min-width: 924px) and (max-width: 1024px) {
	.vhc-load-screen-item-cont {
		height: 40vh !important;
	}
	.filter-row {
		max-width: 55.5%;
		overflow-x: auto;
	}
}

.vhc-save-load-screen {
	display: flex;
	align-items: center;
	justify-content: center;
}
.vhc-load-screen-item-cont {
	margin: 0 !important;
}

/*VERTICAL ORIENTATION*/
@media screen and (orientation:portrait) {
	.vhc-load-screen-item-cont {
		width: 60vw !important;
		margin: 0 !important;
	}
}


.spiff-submit-form>.card-content {
    overflow: scroll;
    height: calc(100% - 40px) !important;
}

