.si-card.list {
	min-width: 97.5%;
	max-width: 97.5%;
	min-height: 88.5vh !important;
	max-height: 88.5vh !important;
}
.si-card.medium-cards {
	min-width: 97.5%;
	max-width: 97.5%;
	min-height: 88vh !important;
	max-height: 88vh !important;
}
.si-card.list>.card-content>.si-info,
.si-card.medium-cards>.card-content>.si-info {
	height: 24vh;
}
#medium-cards>.si-card>.card-content>.si-info>.si-more-info>.si-more-info-items>div>.warning {
	font-size: 11px;
}
.si-card>.card-content>.si-info>.si-more-info>.si-more-info-items>div>.warning {
	font-size: 15px !important;
}
.si-card.list>.card-content>.si-repairs,
.si-card.medium-card>.card-content>.si-repairs {
	min-height:46vh;
	max-height: 46vh;
	display: flex;
	flex-direction: column;
}
.si-card.list>.card-content>.si-repairs>.repair-table,
.si-card.medium-cards>.card-content>.si-repairs>.repair-table {
	min-height: 54vh !important;
	max-height: 54vh !important;
	background-color: #fafafa;
}

/*Notes*/
.si-notes-cont {
	padding: 0px;
	height: 100%;
}
#list>.si-card>.card-content>.si-info>.si-more-info {
	grid-template-columns: 40% 55%;
}

.si-more-info {
	display: grid;
	grid-template-columns: 50% 45%;
	gap: 5%;
	width: 100%;
	height: 100%;
}

.si-notes {
	height: 100%;
	min-height: 150px;
	max-height: 150px;
	width: 100%;
	overflow: hidden;
}
.si-notes>.card-content>textarea {
	min-width: 100%;
	max-width: 100%;
	min-height: 120px;
	max-height: 120px;
	height: 100%;
	border-radius: 0px;
	background-color: #fafafa;
	padding: 10px;
	border: 0px;
	font-size: large;
}
.si-notes>.card-content>textarea:hover {
	background-color: #efefef;
}
.si-notes>.card-content>textarea:focus {
    outline: none;
    background-color: #ccc;
    color: rgb(25,25,25);
}
.si-notes>.card-content {
	padding: 0px;
	width: 100%;
	height: calc(100% - 25px);
}
.si-notes>.card-menubar {
	position: relative;
	height: 25px;
	line-height: 7.5px;
}

@media screen and (orientation:portrait) {
	#medium-cards>.si-card {
		min-height: 54.5vh !important;
		max-height: 54.5vh !important;
	}
	#medium-cards>.si-card>.card-content>.si-repairs {
		padding: 0px;
	}
	#medium-cards>.si-card>.card-content>.si-repairs>#title-row {
		
	}
	#medium-cards>.si-card>.card-content>.si-repairs>#title-row>div,
	#medium-cards>.si-card>.card-content>.si-repairs>#title-row>input {
		
	}
	#medium-cards>.si-card>.card-content>.si-repairs>.repair-table {
		min-height: 67.5% !important;
		max-height: 67.5% !important;
	}
	#medium-cards>.si-card>#undefined-card-content {
		height: 100%;
	}
	#medium-cards>.si-card>#undefined-card-content>div.card-content.si-info {
		height: 40% !important;
		padding: 0px;
	}
	#medium-cards>.si-card>#undefined-card-content>div.card-content.si-repairs {
		height: 60% !important;
	}
	#medium-cards>.si-card>#undefined-card-content>div.card-content.si-info>.si-more-info {
		height: 100%;
	}
	#medium-cards>.si-card>#undefined-card-content>div.card-content.si-info>.si-more-info>.si-notes-cont>.si-notes {
		min-height: 70%;
		max-height: 70%;
	}
	.si-desc>.header-text {
		overflow: hidden;
		display: -webkit-box;
		-webkit-line-clamp: 1; /* number of lines to show */
				line-clamp: 1; 
		-webkit-box-orient: vertical;
	}
	#service-items-detail-card>.card-content {
		display: flex;
		flex-direction: column;
	}
	/*Customize Tablerows*/
	#medium-cards>.si-card>.card-content>.si-repairs>.repair-table>.card-content>.data-table>.tablerow {
		height: fit-content;
		grid-template-columns: 12% 21% 44% 10.5% 2%;
	}
	#medium-cards>.si-card>.card-content>.si-repairs>.repair-table>.card-content>.data-table>.tablerow.other-repair {
		height: fit-content;
		grid-template-columns: 10% 13% 44% 9.25% 10.25% 10%;
	}
	#medium-cards>.si-card>.card-content>.si-repairs>.repair-table {
		min-height: 24vh !important;
		max-height: 24vh !important;
	}
}

/*Surface Go: 200% scaling, 100% browser zoom*/
@media screen and (min-width: 924px) and (max-width: 1024px) {
	#medium-cards>.si-card {
		min-height: 84vh !important;
		max-height: 84vh !important;
	}
	
	#title-row>input {
		width: 50%;
	}
	.si-desc>.header-text {
		overflow: hidden;
		display: -webkit-box;
		-webkit-line-clamp: 1; /* number of lines to show */
				line-clamp: 1; 
		-webkit-box-orient: vertical;
	}
	#list>.si-card {
		min-height: 84vh !important;
		max-height: 84vh !important;
	}
	.si-notes-cont>.si-notes {
		min-height: 100% !important;
		max-height: 100% !important;
	}
	.si-notes-cont>.si-notes>.card-content>textarea {
		overflow-y: scroll;
		min-height: calc(100% - 0px) !important;
		max-height: calc(100% - 0px) !important;
	}
	#list>.si-card>.card-content>.si-repairs>.repair-table,
	#medium-cards>.si-card>.card-content>.si-repairs>.repair-table {
		min-height: 44vh !important;
		max-height: 44vh !important;
	}
	
	#medium-cards>.si-card>.card-content>.si-repairs>.repair-table>.card-content>.data-table>.tablerow {
		font-size: smaller;
		height: fit-content;
		grid-template-columns: 12% 21% 44% 10.5% 2%;
		gap: 0.25%;
	}
	#medium-cards>.si-card>.card-content>.si-repairs>.repair-table>.card-content>.data-table>.tablerow.other-repair {
		font-size: smaller;
		height: fit-content;
		grid-template-columns: 10% 13% 44% 9.25% 10.25% 10%;
		gap: 0.25%;
	}
}