/*Service Item Creator*/
#sitem-creator-card {
	min-height: fit-content;
	max-height: fit-content;
}
#sitem-select-form-item-cont {
	grid-template-columns: 70% 30%;
	gap: 0px;
}
#sitem-select-form-item-cont>.checkbox-cont {
	justify-self: end;
	background-color: #f7f7f7;
}
#sitem-select-form-item-cont>.checkbox-cont:hover {
	background-color: #ccc;
}
#sitem-input-form-cont>input {
	background-color: #f7f7f7;
}
#sitem-input-form-cont>input:hover {
	background-color: #ececec;
}
#sitem-input-form-cont>input:focus {
	outline: none;
    background-color: #ccc;
    color: rgb(25,25,25);
}
#sitem-creator-add-button {
	background-color: #0b4e8a;
}
#sitem-creator-add-button:hover {
	background-color: #07355e;
}