/*COLLATERAL*/
#collat-cont {
	background-color: rgba(0, 0, 0, 0.3);
}

.collateral-card {
	min-width: 90vw;
	max-width: 90vw;
	max-height: 90vh;
	min-height: 90vh;
}

.collateral-cont {
	min-height: 100% !important;
	max-height: 100% !important;
	background-color: #f0f0f0;
	max-width: 100%;
}

.collateral-card>.card-content {
	display: grid;
	grid-template-columns: 70% 30%;
	height: calc(100% - 40px);
}
.collateral-cont>.card-content {
	height: 100%;
	display: flex;
	flex-direction: column;
	gap: 15px;
	align-items: center;
	overflow-y: scroll;
	scrollbar-gutter: 8px;
}

.actions-cont {
	display: flex;
	flex-direction: column;
	gap: 10px;
	align-items: center;
	justify-content: end;
	margin-left: 10px;
}
.actions-cont>.action-button {
	width: 100%;
	height: 50px;
	font-size: x-large;
	line-height: 50px;
}
#complete-ticket-button {
	font-size: large;
}
/*Customize Actions Colors*/
#download-invoice-button {
	display: none;
	background-color: #2d7761;
}
#download-invoice-button:hover {
	background-color: #22614e;
}
#email-invoice-button {
	background-color: #2d3e77;
}
#email-invoice-button:hover {
	background-color: #22305e;
}
#collect-payment-button {
	background-color: #77592d;
}
#collect-payment-button:hover {
	background-color: #5f4723;
}
/*@todo*/
.collateral-print-page {
	background-color: #fff;
	min-width: 95%;
	max-width: 95%;
	max-height: 170.5% !important;
	min-height: 170.5% !important;
	padding: 20px;
	filter: drop-shadow(0.025px 0.5px 1.05px rgba(0, 0, 0, 0.3));
	overflow: hidden;
}

/*2 Paths Invoice page*/
.collateral-cont>.card-content>#invoice-cont {
	display: flex;
	flex-direction: column;
}

.collateral-cont>.card-content>.summary-header {
	width: 100%;
}

/*Emailer*/
#emailer-card {
	width: fit-content;
	min-height: unset;
	height: fit-content;
}

.email-text {
	font-size: x-large;
	text-align: left;
	padding: 5px;
}

#emailer-card-card-content>input {
	width: 100%;
	height: 40px;
	background-color: #fafafa
}
#emailer-card-card-content>input:hover {
	background-color: #ececec;
}
#emailer-card-card-content>input:focus {
	outline: none;
    background-color: #ccc;
    color: rgb(25,25,25);
}
#emailer-card-card-content>.action-row {
	padding-top: 10px;
}

/*Ticket Close Card*/
#ticket-close-card {
	width: max-content;
	height: fit-content;
	min-height: unset;
}
#ticket-close-card-card-content>.form {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap: 10px;
	width: 100%;
	padding: 10px;
	margin-bottom:10px;
}
#ticket-close-card-card-content>.form>.warning {
	width: 100%;
}
#ticket-close-card-card-content>.form>.form-info-item {
	display: grid;
	grid-template-columns: 55% 44%;
	gap: 1%;
	width: 100%;
	align-items: center;
}
#ticket-close-card-card-content>.form>.form-info-item>.checkbox-cont {
	background-color: #fafafa;
	max-width: unset;
	height: 35px;
	width: 35px;
}
#ticket-close-card-card-content>.form>.form-info-item>.checkbox-cont:hover {
	background-color: #ccc;
}
#ticket-close-card-card-content>.form>.form-info-item>input {
	width: 100%;
	height: 40px;
	background-color: #fafafa;
}
#ticket-close-card-card-content>.form>.form-info-item>.dropdown-cont>.dropdown-option.selected {
	background-color: #fafafa;
	height: 35px;font-size: larger;
}
#ticket-close-card-card-content>.form>.form-info-item>.dropdown-cont>.dropdown-option.selected:hover {
	background-color: #ccc;
}
#ticket-close-card-card-content>.form>.form-info-item>.form-info-title {
	font-size: 25px;
	font-weight: 600;
}
#ticket-close-card-card-content>.form>.form-info-item>textarea {
	grid-column: unset;
	min-height: 100px;
	max-height: 100px;
	min-width: 250px;
	max-width: 250px;
	background-color: #fafafa;
	border: 1px solid #ccc;
	padding: 10px;
}
#ticket-close-card-card-content>.form>.form-info-item>textarea:focus {
	background-color: #ccc;
}

#close-ticket-button {
	font-size: larger;
	height: fit-content;
	padding: 10px;
	width: 300px;
}
#close-ticket-button:hover {
	text-transform: uppercase;
	font-weight: 650;
}

div.block {
	background-color: #22614e00;
	width: 100px;
	height: 100px;
	height: 100%;
	width: 100%;
	position: absolute;
	z-index: 200;
}

/*Membership form*/
.collateral-print-page>div>#top-cards>#membership-options-card {
	width: 100%;
}
.collateral-print-page>div>#auth-card-content>#customer-auth-card {
	filter: unset;
	border: 1px solid #ccc;
	min-height: unset;
}

.collateral-print-page>div>#auth-card-content>#customer-auth-card>#customer-auth-card-card-content>#auth-form-cont {
	display: flex;
	flex-direction: column;
	padding: 0;
	margin: 0;
	grid-template-columns: repeat(2, 49.5%);
	gap: 1%;
}
.collateral-print-page>div>#auth-card-content>#customer-auth-card>#customer-auth-card-card-content>#auth-form-cont>.form-info-item {
	grid-template-columns: 30% 69%;
	gap: 1%;
}
.collateral-print-page>div>#auth-card-content>#customer-auth-card>#customer-auth-card-card-content>#auth-form-cont>.form-info-item>.form-info-title {
	font-size: 20px !important;
	text-align: left;
	width: 100%;
	margin: 0;
	padding: 0;
}
#auth-form-cont>.form-info-item>input {
	width: 100%;
	height: 40px;
	border: 0px;
	border-bottom: 1px solid #ccc;
	border-radius: 0px;
}
#form-zip-cont,#form-date-cont {
	display: none;
}

@media screen and (orientation:portrait) {
	.collateral-card {
		min-width: 95vw !important;
		max-width: 95vw !important;
	}
	.collateral-card>.card-content {
		display: flex;
		flex-direction: column;
		height: calc(100% - 40px);
	}
	.collateral-cont {
		min-height: 90% !important;
		max-height: 90% !important;
		height: 90% !important;
	}
	.actions-cont {
		display: flex;
		flex-direction: row;
		height: 10%;
	}
	.actions-cont>.action-button {
		font-size: medium;
	}
	.actions-cont>:nth-child(3) {
		font-size: small !important;
	}
	.actions-cont>:nth-child(4) {
		font-size: x-small !important;
	}
}