/*Checklist summary, no score*/
.checklist-summary-list.no-score>.data-table {
	height: 50% !important;
	border: 0px solid #ccc;
	background-color: transparent;
	border-radius: 0px;
	padding: 0px;
	display: flex;
	flex-direction: column;
	font-size: 15px;
	align-content: center;
	justify-content: center;
	overflow: hidden;
	scrollbar-gutter: unset;
}
.checklist-summary-list.no-score {
	height: 100%;
	overflow: hidden;
	scrollbar-gutter: unset;
}
.checklist-summary-list.no-score>.tablerow-container>.table-row {
	border-bottom: 1px solid #ccc;
	border-right: 0px solid #ccc;
	grid-template-columns: 50% 50%;
	padding: 0px !important;
	width: 100%;
	background-color: transparent;
	height: fit-content;
	align-items: center;
	justify-content: center;
}
.checklist-summary-list.no-score>.tablerow-container>.table-row>div {
	padding: 5px;
}
.collateral-print-page>.card>#ChecklistSummaryCard-card-content {
	scrollbar-gutter: unset;
	overflow-y: auto;
}
.collateral-print-page>.card>#ChecklistSummaryCard-card-content>.card-content {
	overflow: hidden;
	padding: 0px;
	scrollbar-gutter: unset;
	height: 100%;
}
.collateral-print-page>#ChecklistSummaryCard {
	overflow: hidden;
}

@media print {
	.collateral-print-page>.card {
		max-width: 100%;
		min-width: 100%;
		overflow: hidden !important;
	}
}