.collateral-print-page>#invoice-cont {
	position: relative;
	display: block!important;
	flex-direction: column !important;
	display: block;
	height: 100%;
}
.collateral-print-page>#invoice-cont>#membership-opt-cont {
	display: none;
}

.collateral-print-page>#invoice-cont>#RepairSummaryCard>#RepairSummaryCard-card-content>div:nth-child(3) {
	height: min-content !important;
}
.collateral-print-page>#invoice-cont>#RepairSummaryCard {
	height: 50% !important;
	min-height: 50% !important;
	max-height: 50% !important;
	min-width: 100%;
	max-width: 100%;
	overflow: hidden;
}

.collateral-print-page>#invoice-cont>.four-fifths-top-column {
	min-height: 42.5%;
	max-height: 42.5% !important;
	display: block;
	page-break-after: always;
	margin-top: 5%;
	display: none;
}

.collateral-print-page>#invoice-cont>.four-fifths-top-column>#membership-options-card>.card-content {
	display: grid !important;
	grid-template-columns: 14% 43% 43% !important;
}

.collateral-print-page>#invoice-cont>.four-fifths-top-column>#logo-card {
	display: none;
}

.collateral-print-page>#invoice-cont>.four-fifths-top-column>#membership-options-card>#membership-options-card-card-content>.card-content>.plan-form>.double-form-item {
	display: grid;
	grid-template-columns: 85% 10%;
	gap: 5%;
}

.collateral-print-page>#invoice-cont>.four-fifths-top-column>#membership-options-card>#membership-options-card-card-content>.card-content>.plan-form>.double-form-item>:nth-child(1) {
	display: grid;
	grid-template-columns: 65% 30%;
	gap: 5%;
}
.collateral-print-page>#invoice-cont>.four-fifths-top-column>#membership-options-card>#membership-options-card-card-content>.card-content>.plan-form>.double-form-item>:nth-child(1)>input {
	width: 100%;
	border: 0px;
	border-radius: 0px;
	border-bottom: 1px solid black;
}

.collateral-print-page>#invoice-cont>.four-fifths-top-column>#membership-options-card>#membership-options-card-card-content>.card-content>.plan-form>#monthly-plan-field {
	display: grid;
	grid-template-columns: 45% 45%;
}
.collateral-print-page>#invoice-cont>.four-fifths-top-column>#membership-options-card>#membership-options-card-card-content>.card-content>.plan-form>#monthly-plan-field>:nth-child(1) {
	display: flex !important;

}

.collateral-print-page>#invoice-cont>.four-fifths-top-column>#membership-options-card>#membership-options-card-card-content>#membership-options-total-cont>#terms-button {
	display: none;
}

.collateral-print-page>#invoice-cont>.four-fifths-top-column>#membership-options-card>#membership-options-card-card-content>#membership-options-total-cont {
	position: relative;
	left: 40px;
}

.collateral-print-page>#invoice-cont>.four-fifths-top-column>#membership-options-card>#membership-options-card-card-content>:nth-child(1)>.form-info-item {
	display: flex !important;
	flex-direction: column !important;
}
.collateral-print-page>#invoice-cont>.four-fifths-top-column>#membership-options-card>#membership-options-card-card-content>:nth-child(1)>.form-info-item>.form-info-title {
	text-align: left;
	color:#000 !important;
}
.collateral-print-page>#invoice-cont>.four-fifths-top-column>#membership-options-card>#membership-options-card-card-content>:nth-child(1)>.form-info-item>.form-info-title::after {
	content: none;
}
.collateral-print-page>#invoice-cont>.four-fifths-top-column>#membership-options-card>#membership-options-card-card-content>:nth-child(1)>.form-info-item>input {
	background-color: transparent;
}

/*Terms & Conditions*/
.collateral-print-page>#terms-card {
	filter: unset;
	width: 100% !important;
	min-width: 100% !important;
	max-width: 100% !important;
}
.collateral-print-page>#terms-card>.card-menubar {
	display: none;
}
.collateral-print-page>#terms-card>.card-content {
	width: 100%;
	padding: 0px;
	gap: 5px;
}
.collateral-print-page>#terms-card>.card-content>.paragraph-cont>.paragraph-header {
	font-size: 18px;
}
.collateral-print-page>#terms-card>.card-content>.paragraph-cont>.paragraph-body {
	font-size: 14.25px;
}
.collateral-print-page>#terms-card>.card-content>.paragraph-cont>br {
	display: none;
}

@media print {
	#terms-card {
		margin-top: 20px;
		display: block;
		border: 0px;
	}
}