/*WO INFORMATION*/
#wo-info-card {
	height: calc(100% - 20px);
	min-width: calc(100% - 20px);
	max-width: calc(100% - 20px);
	font-size: larger;
}
.wo-information-top{
	display: grid;
	grid-template-columns: 45% 55%;
}
.wo-info-customer-info {
	width: 100%;
	font-size: larger;
	color: #212121;
	text-align: left;
	padding: 0px;
}

.wo-info-customer-info>.warning>img {
	height: 20px;
}
.wo-info-customer-info>.warning {
	padding: 0px;
	height: 30px;
}
#cust-info-customername {
	text-align: left;
}

#cust-info-contactphone {
	font-style: italic;
	font-size: smaller;
}

#cust-info-custcode>div {
	font-size: 45px;
}
#wo-descr-box {
	min-width: 100%;
	max-width: 100%;
	min-height: 290px;
	max-height: 290px;
	font-size: 20px;
	background-color: #fcfcfc;
	padding: 10px;
	border: 1px solid #ccc;
}
#wo-info-card-card-content>textarea:focus{
    outline: none;
    background-color: #ccc !important;
    color: rgb(25,25,25);
}


#wo-history-card, #wo-info-card {
	min-height: 96vh;
	max-height: 96vh;
}

#wo-history-card {
	display: none;
}

#wo-history-card>.card-menubar {
	position: relative;
}
#wo-history-card>.card-content {
	height: calc(100% - 40px);
}

#email-form-item {
	display: grid;
	grid-template-columns: 100% 0%;
	gap: 0px;
	align-items: center;
	font-size: 20px;
	gap: 2.5%;
}
#email-form-item>.form-info-title {
	width: 100%;
	text-align: left;
}
#cust-info-contactemail {
	width: 80%;
	padding: 10px;
	margin-top: 7.5px;
	background-color: #fafafa;
}
#cust-info-contactemail:focus {
	outline: none;
    background-color: #ccc;
    color: rgb(25,25,25);
}

#sales-info {
	background-color: #fcfcfc;
	border-radius: 5px;
	display: grid;
	grid-template-columns: 50% 50%;
	height: fit-content;
	margin-top: 10px;
	margin-right: 10px;
}
#sales-info>.form-info-item {
	padding: 5px;
	display: grid;
	grid-template-columns: 49.5% 49.5%;
	gap: 1%;
	align-items: center;
	justify-content: center;
	gap: 0px;
	border-radius: 10px;
	font-size: 15px;
	border: 1px solid transparent;
}
#sales-info>.form-info-item>div {
	width: 100%;
}
#sales-info>.form-info-item:hover {
	background-color: #f0f0f0;
	border: 1px solid #ccc;
}

#wo-pbpl-info {
	display: flex;
	padding-left: 10px;
	padding-right: 10px;
	margin-bottom: 5px;
}
#wo-pbpl-info>.form-info-item {
	display: grid;
	grid-template-columns: 70% 30%;
	width: 27%;
	align-items: center;
	justify-content: center;
}
#wo-pbpl-info>.form-info-item>:nth-child(2) {
	font-weight: 600
}

/*CONTRACT CARD*/
#contract-form-card {
	width: 50vw;
	min-height: unset;
	min-height: fit-content;
	max-height: 92.5vh;
}
#contract-form-card-card-content {
	display: flex;
	flex-direction: column;
	row-gap: 10px;
	height: calc(100% - 40px);
	overflow-y: scroll;
}
#contract-form-card-card-content>.form-info-item>input {
	background-color: #fafafa;
}
#contract-form-card-card-content>.form-info-item>input:hover {
	background-color: #ececec;
}
#contract-form-card-card-content>.form-info-item>input:focus {
	background-color: #ccc;
}
#contract-close-button {
	align-self: flex-end;
	margin-top: 10px;
	width: 40%;
}

/*Price level form*/
#pl-form-card {
	min-height: max-content;
	min-width: fit-content;
	max-width: max-content;
}

#pl-info-text {
	font-size: 25px;
	padding: 5px;
}
#pl-info-text>span {
	font-weight: 600;
	font-size: 30px;
}
#pl-change-form-item-cont {
	display: grid;
	grid-template-columns: 50% 350px;
	font-size: 25px;
	padding: 10px;
	align-items: center;
	justify-content: center;
	padding-bottom: 20px;
}
#pl-change-form-item-cont>:nth-child(2) {
	width: 300px;
	justify-self: end;
}
#pl-change-form-item-cont>:nth-child(2)>.dropdown-option.selected {
	background-color: #fafafa;
}
#pl-change-form-item-cont>:nth-child(2)>.dropdown-option.selected:hover {
	background-color: #ccc;
}
#pl-change-form-item-cont>.form-info-title {
	max-lines: 1;
	width: 100%;
}
#pl-form-card>.card-content>.action-row>.action-button {
	height: 40px;
	line-height: 40px;
	font-size: larger;
}

#pl-update-button {
	background-color: #5983d1;
}
#pl-update-button:hover {
	background-color: #456bb3;
}

#cust-header-info {
	align-items: end;
}

#contract-row {
	align-items: center;
}

/*Surface Go: 200% scaling, 100% browser zoom*/
@media screen and (min-width: 924px) and (max-width: 1024px) {
	#wo-info-card {
		min-height: 94vh;
		max-height: 94vh;
	}
	#pricing-book-info {
		display: flex;
	}
	#pricing-book-info>:nth-child(2) {
		display: flex;
		gap: 0px;
		width: 50%;
	}
	#wo-pbpl-info {
		width: 100%;
	}
	#wo-pbpl-info>.form-info-item {
		width: 100%;
		display: grid;
		grid-template-columns: 80% 20% !important;
	}

	#wo-descr-box {
		min-height: 120px !important;
		max-height: 120px !important;
	}
	#contract-form-card {
		height: 90%;
		align-self: center;
	}
	#contract-form-card-card-content {
		height: calc(100% - 40px);
		display: flex;
		flex-direction: column;
		overflow-y: auto;
	}

	.wo-info-customer-info {
		width: 100%;
		font-size: large;
		color: #212121;
		text-align: left;
		padding: 0px;
	}
	#cust-info-custcode>div {
		font-size: 40px;
	}
}

/*Portrait Mode*/
@media screen and (orientation:portrait) {
	#sales-info {
		height: 85%;
		display: flex;
		flex-direction: column;
		width: 90%;
		margin-left: 5%;
	}
}