.exit-dialogue-card {
	height: fit-content !important;
	min-height: unset;
}

.exit-message {
	padding: 10px;
	font-size: x-large;
	text-align: left;
	font-weight: 650;
}

.exit-dialogue-confirm,.exit-dialogue-cancel {
	font-size: x-large;
	height: 40px;
	width: 150px;
	line-height: 40px;
}

.exit-dialogue-confirm {
	font-weight: 700;
	background-color: #43a560;
}
.exit-dialogue-confirm:hover {
	background-color: #2e7042;
}

.exit-dialogue-cancel {
	font-weight: 300;
	background-color: #436da5;
}
.exit-dialogue-cancel:hover {
	background-color: #2e4b70;
}