@media print {
	body {
		text-align: center;
	}
	#collat-cont {
		background-color:transparent;
	}
	#signature-button {
		display: none;
	}
	.block {
		background-color: rgba(0, 0, 0, 0.5);
	}
	.collateral-print-page {
		background-color: transparent;
		min-width: 100%;
		max-width: 100%;
		padding: 0px;
		display: inline;
		border: 0px;
		filter: unset;
		min-height: unset !important;
		max-height: unset !important;
		height: fit-content;
	}
	.side-bar-menu-cont {
		display: none;
	}
	.app-container>.action-row {
		display: none;
	}
	.app-container>.score-card-container {
		display: none !important;
	}
	.app-container>#signature-button {
		display: none;
	}
	.side-bar-container#collapsed {
		width: 100vw !important;
		min-width: 100vw;
		max-width: 100vw;
		overflow-y: visible;
	}
	.side-bar-container {
		width: 100vw !important;
		min-width: 100vw;
		max-width: 100vw;
	}
	.app-container {
		position: absolute;
		left: 0px !important;
		padding: 0px;
		width: 100vw !important;
		min-width: 100vw;
		max-width: 100vw;
	}
	.collateral-card {
		min-width: 100%;
		max-width: 100%;
		width: 100%;
		padding: 0px;
		filter: unset;
		margin: 0px;
		display: block;
		min-height: unset;
		max-height: unset;
	}
	.collateral-card>.card-menubar {
		display: none;
	}
	.collateral-card>.card-content {
		width: 100%;
		padding: 0px;
		display: block;
	}
	.actions-cont {
		display: none !important;
	}
	.collateral-cont {
		width: 100%;
		max-width: 100%;
		padding: 0px;
		background-color: transparent;
		border: 0px;
		overflow-y: unset;
		padding: 0px;
	}
	.collateral-cont>.card-content {
		position: absolute !important;
		top: 0;
		left: 0;
		display: block;
		width: 100% !important;
		padding: 0px;
		overflow-y:visible !important;
		height: 100% !important;
		min-height: unset !important;
		max-height: unset !important;
	}
	.collateral-print-page>#invoice-cont {
		height: unset !important;
	}
	.collateral-print-page {
		display: block !important;
		page-break-after: always;
	}
	#sig-cont {
		display: none !important;
	}
	#main-dash {
		display: none;
	}
}