/*LEGEND*/
.chart-legend {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	height: 140px;
	width: 80%;
}

.legend-item {
	display: flex;
	align-items: center;
	gap: 5px;
	margin: 2.5px;
}

.legend-color {
	width: 5px;
	height: 15px;
}

.legend-text {
	font-size: 14.5px;
	font-weight: 550;
	text-align: left;
}

.legend-color#excellent {
	background-color: #00c602;
}
.legend-color#minorimprovementsavailable,.legend-color#good {
	background-color: #fff700;
}
.legend-color#improvementsavailable, .legend-color#average {
	background-color: #ffd200;
}
.legend-color#severalimprovementsrecommended, .legend-color#considerreplacement {
	background-color: #ff8800;
}
.legend-color#addressconcernssoon, .legend-color#replacementrecommended {
	background-color: #de3b04;
}

/*Surface Go: 200% scaling, 100% browser zoom*/
@media screen and (min-width: 924px) and (max-width: 1024px) {
	.legend-text {
		font-size: 9.5px;
		font-weight: 550;
		text-align: left;
	}
}