#spiff-chart-card>.card-menubar {
	position: relative;
}

#spiff-chart-cont {
	height: 52.5%;
	max-height: 50%;
	margin-top: 2.5%;
	font-size: x-large;
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 5%;
	padding: 10px;
}

#spiff-sumdata-cont {
	height: 45%;
	width: 100%;
	display: flex;
	grid-template-columns: 50% 50%;
	padding: 20px;
}
#spiff-sumdata-cont>.card-content {
	display: flex;
	flex-direction: column;
	gap: 10%;
	width: 100%;
	height: 90%;
}
#spiff-sumdata-cont>.card-content {
	min-width: 100%;
	padding: 0px;
	overflow: hidden;
	height: 100%;
}
#spiff-sumdata-cont>.card-content>.action-row {
	gap: 0.5%;
	width: 100%;
	display: grid;
	grid-template-columns: 11% 14% 11% 31% 31%;
	height: 50%;
}

.total-payout-cont.form-info-item {
	font-size: 20px;
	display: flex;
	flex-direction: column;
	gap: 0px;
	justify-content: start;
}
.total-payout-cont.form-info-item>.form-info-title {
	font-weight: 300;
	font-style: italic;
}
.total-payout-cont.form-info-item>:nth-child(2) {
	font-weight: 600;
	font-size: 45px;
	color: #303030;
}
.big-form-item>.total-payout-cont.form-info-item>.form-info-title {
	font-size: 25px;
	font-weight: 500;
}
.big-form-item>.total-payout-cont.form-info-item>:nth-child(2) {
	font-size: 55px;
	font-weight: 900;
	color: #000;
}
#spiff-payout-total>:nth-child(2)::before {
	content: '$';
}
#spiff-win-rate>:nth-child(2)::after {
	content: '%';
}

#spiff-freq-won, #spiff-freq-lost {
}

#spiff-freq-won>:nth-child(2), #spiff-freq-lost>:nth-child(2) {
	font-size: 22.5px;
}

.spiff-data-cont {
	height: calc(100% - 0px);
	overflow-y: auto;
	overflow-x: hidden;
}

/*Portrait Mode*/
@media screen and (orientation:portrait) {
	#spiff-chart-cont {
		flex-direction: column;
		min-height: 75% !important;
		max-height: 75% !important;
	}
}