/*Popout Repair Table*/
.repair-table-popout-card {
	min-width: 90vw;
	min-height: 70vh;
	max-height: 70vh;
	height: 70vh;
	position: absolute;
	bottom: 10px;
	margin-left: 5vw;
}
.repair-table-popout-card>.card-content {
	height: calc(70vh - 40px);
	overflow:visible;
}
#repair-checkboxes {
	width: 70%;
	display: grid;
	grid-template-columns: 25% 40% 30%;
}
#repair-checkboxes>.form-info-item {
	display: flex !important;
	flex-direction: row-reverse;
	align-items: center;
}
#repair-checkboxes>.form-info-item>.form-info-title {
	width: 100%;
	font-weight: 600;
}
.repair-table-popout-card>.card-content>.action-row {
	height: 50px;
	display: flex;
	align-items: center;
}
#repair-table-inputs {
	align-self: flex-end;
	justify-self: flex-end;
}
#repair-table-inputs>.form-info-item>input {
	background-color: #fafafa;
}
#repair-table-inputs>.form-info-item>input:hover {
	background-color: #ececec;
}
#repair-table-inputs>.form-info-item>input:focus {
	background-color: #ccc;
}
#repair-table-repair-cont {
	height: calc(100% - 90px);
	padding: 0;
}
#repair-table-repair-cont>.tablerow-container {
	height: 100% !important;
	overflow-y: scroll;
}
#repair-table-repair-cont>.table-row.header {
	grid-template-columns: 5% 10% 60% 15% 10%;
	font-weight: 550;
	border-bottom: 1px solid #ccc;
	font-size: 1em;
	padding: 0px;
	margin: 0px;
	height: 40px;
}
#repair-table-repair-cont>.table-row.header>.rowitem {
	background-color: #fff;
	border-right: 0px;
}
#repair-table-repair-cont>.tablerow-container>.table-row {
	height: fit-content;
	cursor: pointer;
	border-bottom: 1px solid #ccc;
	display: grid;
	padding: 5px !important;
	align-items: center;
	margin: 0px;
	grid-template-columns: 5% 10% 60% 15% 10%;
	font-size: 17.5px;
}
#repair-table-repair-cont>.data-table>.tablerow>div {
	margin: 0px;
}
#repair-table-repair-cont>.data-table>.table-row:hover {
	background-color: #ccc;
}

/*Surface Go: 200% scaling, 100% browser zoom*/
@media screen and (min-width: 924px) and (max-width: 1024px) {
	.repair-table-popout-card {
		min-width: 95vw;
		min-height: 80vh;
		max-height: 80vh;
		position: absolute;
		bottom: 10px;
		margin-left: 2.5vw;
		overflow: hidden;
	}
	.repair-table-popout-card>.card-content {
		height: calc(80vh - 40px);
	}
	.repair-checkboxes>.form-info-item {
		grid-template-columns: 20% 80%;
		font-size: smaller;
	}
}

/*Portrait Mode*/
@media screen and (orientation:portrait) {
	.repair-table-popout-card {
		min-width: 95vw;
		min-height: 65vh;
		max-height: 65vh;
		position: absolute;
		bottom: 10px;
		margin-left: 2.5vw;
		overflow: hidden;
	}
	.repair-table-popout-card>.card-content {
		height: calc(65vh - 40px);
	}
	#repair-table-inputs {
		align-items: end;
		width: 100%;
	}
	.repair-table-checks {
		background-color: #eaeaea;
		border: 1px solid #ccc !important;
		height: 90px;
		display: flex;
		flex-direction: column !important;
	}
	.repair-table-checks>#repair-checkboxes {
		width: 100%;
	}
	#repair-table-repair-cont {
		height: calc(100% - 130px) !important;
	}
	#repair-table-repair-cont>.tablerow-container {
		height: calc(100% - 15px) !important;
	}
	#repair-table-repair-cont>.table-row.header {
		height: 60px;
		grid-template-columns: 10% 20% 40% 15% 15%;
	}
	#repair-table-repair-cont>.tablerow-container>.table-row {
		grid-template-columns: 10% 20% 40% 15% 15%;
	}
}