.invoice-page-cont>img {
	width: 80%;
}

.invoice-cust-info {
	border: 1px solid #ccc;
	border-radius: 5px;
	display: flex;
}
.invoice-location-info {
	width: 50%;
	display: flex;
	flex-direction: column;
	border-right: 1px solid #ccc;
	text-align: left;
}

.invoice-cust-location {
	border-bottom: 1px solid #ccc;
	height: 50%;
	padding:5px;
}
.invoice-job-location {
	padding: 5px;
	height: 50%;
}

.invoice-invoice-info {
	width: 50%;
	padding:5px;
	display: flex;
	flex-direction: column;
}
.invoice-item {
	display: grid;
	grid-template-columns: 50% 50%;
	text-align: left;
}
.invoice-item>:first-child {
	font-weight: 600;
}

.invoice-service-notes {
	border: 1px solid #ccc;
	border-radius: 5px;
	padding: 10px;
	margin-top: 5px;
	margin-bottom: 5px;
	text-align: left;
}
.service-notes-header {
	font-weight: 600;
}

.invoice-summary {
	border: 1px solid #ccc;
	border-radius: 5px;
	min-height: 4.25in;
	overflow: hidden;
}
.invoice-repairs-summary {
	min-height: 3in;
	max-height: fit-content;
	border-bottom: 1px solid #ccc;
}

.signature-total-box {
	display: grid;
	grid-template-columns: 50% 50%;
	height: 0.75in;
}
.signature-box {
	border-right: 1px solid #ccc;
	height: 100%;
	overflow: hidden;
}
.signature-box>img {
	height: 50%;
}

.invoice-total {
	display: grid;
	grid-template-columns: 30% 40%;
	align-items: center;
	justify-content: center;
	padding: 10px;
}

.invoice-total>:nth-child(2) {
	border-bottom: 1px solid #ccc;
	width: 100%;
	text-align: right;
	font-size: 22.5px;
}

.invoice-footer {
	background-color: #0f4677;
	color: #fff;
	font-size: 40px;
	font-weight: 600;
}

.inv-repair-table {
	padding: 5px;
}

.repair-header-row {
	border-bottom: 1px solid #ccc;
	display: grid;
	grid-template-columns: 55% 15% 15% 15%;
	padding: 10px;
	font-weight: 600;
	background-color: #0f4677;
	color: #fff;
}

.repair-row-title {
	font-weight: 600;
	font-style: italic;
	text-align: left;
}
.inv-repair-row {
	display: grid;
	grid-template-columns: 55% 15% 15% 15%;
	padding-top: 5px;
	padding-bottom: 5px;
	font-size: larger;
}
.inv-repair-row>:nth-child(3)::before {
	content: '$';
}

