#spiff-menu-bar,
#spiff-dash-menu-bar {
	position: relative;
	top: unset;
}

#spiff-sum-card {
	overflow: hidden;
	height: calc(100% - 65px) !important;
	min-width: 99%;
}
#spiff-sum-card>.card-menubar {
	position: relative;
}
#spiff-sum-card>.card-content {
	padding: 0px;
	height: calc(100% - 80px) !important;
}


#spiff-card {
	overflow: hidden;
	min-height: 85vh;
	max-height: 85vh;
	height: 85vh !important;
	min-width: 70%;
}
#spiff-card>.card-content {
	padding: 0px;
}
#spiff-card>.card-menubar {
	position: relative;
}

/*GRID*/
#spiff-card-card-content>.tablerow.header,
#spiff-card-card-content>.card-content>.tablerow {
	display: grid;
	grid-template-columns: 10% 10% 10% 10% 10% 10% 20% 20%;
	align-items: center;
	justify-content: center;
}

#spiff-card-card-content>.tablerow.header {
	background-color: #efefef;
	padding: 0px;
	border-bottom: 1px solid #ccc;
}
#spiff-card-card-content>.tablerow.header>div {
	background-color: transparent;
	font-weight: 600;
	line-height: 40px;
}

#spiff-card-card-content>.card-content {
	overflow: unset;
	padding: 0px;
	overflow-y: auto;
	height: calc(85vh - 120px) !important;
}
#spiff-card-card-content>.card-content>.tablerow {
	height: fit-content;
	border-bottom: 1px solid #e1e1e1;
	padding: 0px;
	padding-top: 10px;
	padding-bottom: 10px;
	font-size: 14px;
}
#spiff-card-card-content>.card-content>.tablerow:hover {
	background-color: #f9f9f9;
} 

#spiff-menu-bar>.menu-button,
#spiff-dash-menu-bar>.menu-button {
	background-color: #114069;
	border-radius: 0px;
	align-items: center;
	justify-content: center;
	font-size: x-large;
	font-weight: 550;
}
#spiff-menu-bar>.menu-button:hover,
#spiff-dash-menu-bar>.menu-button:hover,
#spiff-dash-menu-bar>.menu-button.selected {
	background-color: #062a49;
}
#spiff-menu-bar>.menu-button {
	color: #fff;
}
#spiff-menu-bar>:nth-child(1) {
	background-color: #114069;
}
#spiff-menu-bar>:nth-child(2) {
	background-color: #6e2828;
}
#spiff-menu-bar>:nth-child(3) {
	background-color: #335856;
}
#spiff-menu-bar>:nth-child(4) {
	background-color: #4c3358;
}

#spiff-menu-bar>:nth-child(1):hover,
#spiff-menu-bar>:nth-child(1).selected {
	background-color: #062a49;
}
#spiff-menu-bar>:nth-child(2):hover,
#spiff-menu-bar>:nth-child(2).selected {
	background-color: #4d1919;
}
#spiff-menu-bar>:nth-child(3):hover,
#spiff-menu-bar>:nth-child(3).selected {
	background-color: #254746;
}
#spiff-menu-bar>:nth-child(4):hover,
#spiff-menu-bar>:nth-child(4).selected {
	background-color: #34203d;
}


.spiff-actions {
	padding: 10px;
}

#spiff-past-report-card {
	filter: unset;
	border: 1px solid #ccc;
}
#spiff-past-report-card>.card-menubar {
	position: relative;
}
#spiff-past-report-card-card-content {
	padding: 0px;
}
#spiff-past-report-card-card-content>.tablerow.header {
	background-color: #efefef;
	grid-template-columns: 25% 25% 25% 25%;
	padding: 0px;
	height: 100%;
	font-size: small;
	line-height: 20px;
	border-bottom: 1px solid #ccc;
}
#spiff-past-report-card-card-content>.tablerow.header>div {
	background-color: transparent;
	border-right: 1px solid #ccc;
	margin: 0px;
	padding: 10px;
}
#spiff-past-report-card-card-content>.tablerow.header>:last-child {
	line-height: 10px;
}
/*Surface Go: 200% scaling, 100% browser zoom*/
@media screen and (min-width: 924px) and (max-width: 1024px) {
	#spiff-sumdata-cont>.card-content {
		display: flex;
		flex-direction: row !important;
		align-items: end;
		gap: 0 !important;
	}
	.total-payout-cont.form-info-item {
		font-size: smaller;
	}
	#spiff-freq-won>:nth-child(2), #spiff-freq-lost>:nth-child(2) {
		font-size: 14px !important;
	}
	#spiff-sumdata-cont>.card-content>.action-row {
		grid-template-columns: 14% 16% 12% 30% 24% !important;
		align-items: start;
	}
	.total-payout-cont.form-info-item>.form-info-title {
		font-size: 14px;
	}
	.total-payout-cont.form-info-item>:nth-child(2) {
		font-size: 20px !important;
	}
	.big-form-item>.total-payout-cont.form-info-item>.form-info-title {
		font-size: 18px !important;
	}
	.big-form-item>.total-payout-cont.form-info-item>:nth-child(2) {
		font-size: 40px !important;
	}
}

/*Portrait Mode*/
@media screen and (orientation:portrait) {
	#spiff-sumdata-cont>.card-content>.action-row {
		display: grid;
		grid-template-columns: repeat(3, 33%) !important;
		height: 100% !important;
	}
}