/*Invoice Summary*/
#invoice-cont {
	grid-template-columns: 65% 34%;
}
#InvoiceSummaryCard>.card-menubar {
	position: relative;
}
#InvoiceSummaryCard-card-content {
	position: relative;
	height: calc(100% - 40px);
	padding: 0px;
}
.invoice-total-cont {
	font-size: 20px;
	height: 100%;
}
.invoice-total-row {
	display: grid;
	grid-template-columns: 40% 12.5% 17.5% 12.5% 17.5%;
	font-size: 25px !important;
}
.invoice-total-row>div {
	padding: 10px;
	font-weight: 550;
}
#reg-due-today {
	font-weight: 500;
}
#inv-save-today>:nth-child(2) {
	font-weight: 500;
}
.invoice-total-row>:first-child {
	text-align: left;
}
.invoice-total-row>.price::before {
	content: '$';
}
#inv-labels {
	border-bottom: 1px solid #ccc;
	font-size: 30px;
}
.mem-type-label {
	font-style: italic;
}
#mem-due-today {
	font-weight: 650;
}
#price-level-label {
	text-transform:uppercase;
	font-weight: 600;
	animation-name: textshine;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    animation-direction: alternate-reverse;
	background-color: /*#f0f0f0;*/#86ff8c;
}

/*Adjustments to Membership Form*/
.double-form-item>.form-info-item>input {
	width: 25%;
}

/*Surface Go: 200% scaling, 100% browser zoom*/
@media screen and (min-width: 924px) and (max-width: 1024px) {
	.invoice-total-row {
		font-size: smaller !important;
	}
}

/*Portrait Mode*/
@media screen and (orientation:portrait) {
	#invoice-cont {
		grid-template-columns: unset;
		grid-template-rows: 65% 35%;
	}
	#invoice-cont>.pres-left-full {
		width: 100%;
	}
	#logo-card {
		display: none;
	}
	#membership-opt-cont {
		display: flex;
	}
	#cl-start-over-button {
		font-size: 15px !important;
	}
	#RepairSummaryCard-card-content {
		overflow: hidden;
	}
	#RepairSummaryCard-card-content>.invoice-total-cont.sig-box {
		height: 31% !important;
	}
	#RepairSummaryCard-card-content>.invoice-total-cont.sig-box>.invoice-total-row {
		font-size: 20px !important;
	}
	#RepairSummaryCard-card-content>:nth-child(3) {
		height: calc(69% - 50px) !important;
	}
}