.notes-title {
	font-weight: 600;
	font-size: x-large;
	text-align: left;
}

.sitem-print-notes>:nth-child(2) {
	font-size: larger;
}

.sitem-print-card {
	height: fit-content;
	max-height: unset;
	min-height: unset;
	text-align: left;
}
.sitem-print-card>.card-menubar {
	position: relative;
}

@media print {
	.sitem-print-card {
		min-width: 100%;
		width: 100%;
	}
}

.sitem-print-repairs-table>div>div>div {
	padding-top: 5px;
	padding-bottom: 5px;
	font-size: 17.5px;
}

.repair-print-page-cont {
	display: flex;
	flex-direction: column;
	gap: 10px;
}