.test-cont {
	width: 60vw;
	height: 92.5vh;
	background-color: #f0f0f0;
	position: absolute;
	overflow-y: scroll;
}

.system-health-report-cont {
	display: flex;
	flex-direction: column;
	gap: 10px;
	width: 100%;
	height: 100%;
}

.system-health-report-cont>.summary-title-header {
	height: min-content;
}

.system-health-report-cont>.score-breakdown-cont {
	display: flex;
	flex-direction: column;
	gap: 10px;
	page-break-after: always;
}

.system-health-report-cont>.score-breakdown-cont>#SystemScoreSummaryCard>.card-content>.card-content>.bar-chart-container {
	display: none;
}

.system-health-report-cont>.score-breakdown-cont>.bar-chart-card>.card-menubar {
	position: relative;
}
.system-health-report-cont>.score-breakdown-cont>.bar-chart-card {
	min-height: min-content;
	width: 100%;
	min-width: 100%;
}
.system-health-report-cont>.score-breakdown-cont>.bar-chart-card>.card-content>.bar-chart-container>.bar-chart>.bar-chart-bars-cont {
	gap: 10px !important;
}

.system-health-report-cont>.score-breakdown-cont>#SystemScoreSummaryCard {
	min-height: unset !important;
	max-height: unset !important;
	height: fit-content;
	width: 100%;
	min-width: 100%;
}
.system-health-report-cont>.score-breakdown-cont>#SystemScoreSummaryCard
.system-health-report-cont>.score-breakdown-cont>#SystemScoreSummaryCard>.card-content>.card-content>.guage-cont>#gauge-chart1>svg {
	width: 300px !important;
	height: 100px !important;
}

.score-summary-text-cont {
	text-align: left;
	display: none;
}