/*CHECKLIST FORM CUSTOMIZATIONS*/
/*View customizations*/
.checklists-list#list>.card>.card-content>.card-content {
	display: grid;
	grid-template-columns: 47.5% 50%;
	display: flex;
	flex-direction: column;
	column-gap: 2.5%;
	height: fit-content;
	overflow: visible;
	padding-top: 0px;
	padding-bottom: 0px;
}
.checklists-list#list>.card>.card-content {
	padding-top: 0px;
	padding-bottom: 0px;
}
.checklists-list#list>.card>.card-content>.card-content>:nth-child(2n + 1) {
	/*padding-right:  2.5%;
	border-right: 1px solid #ccc;*/
}
.checklists-list#list>.card>.card-content>.card-content>.checklist-form-info-item {
	padding-top: 5px;
	padding-bottom: 5px;
}
.calculation-form-item {
	display: flex;
	align-items: center;
}
.calculation-form-item>.form-info-item,
.checklist-form-info-item>.form-info-item {
	width: 100%;
	padding-top: 5px;
	padding-bottom: 5px;
	font-size: 30px;
	display: grid;
	grid-template-columns: 60% 40%;
}
.checklist-form-info-item>.form-info-item {
	grid-template-columns: 55% 45%;
	align-items: center;
	text-align: left;
}
.checklist-form-info-item>.form-info-item>.checkbox-cont {
	background-color: #fafafa;
	max-width: unset;
	height: 30px;
	width: 30px;
}
.checklist-form-info-item>.form-info-item>.checkbox-cont:hover {
	background-color: #ccc;
}
.checklist-form-info-item>.form-info-item>input,
.checklist-form-info-item>.form-info-item>.dropdown-cont>.dropdown-option.selected {
	background-color: #fafafa;
}
.checklist-form-info-item>.form-info-item>.dropdown-cont>.dropdown-option.selected:hover {
	background-color: #ccc;
}
.checklist-form-info-item>.form-info-item>input:hover {
	background-color: #ececec;
}
.checklist-form-info-item>.form-info-item>input:focus {
	outline: none;
    background-color: #ccc;
    color: rgb(25,25,25);
}
.checklist-form-info-item.instruction>.form-info-item {
	grid-template-columns: 95% 5%;
	text-align: left;
}
.checklist-form-info-item.instruction>.form-info-item {
	font-style: italic;
}
.checklist-form-info-item.rewards {
	background-color: transparent;
}
.checklist-form-info-item.rewards>.form-info-item>.form-info-title {
	font-weight: 300;
	font-style: italic;
}
.checklist-form-info-item.rewards>.form-info-item>.form-info-title>.warning>.warning-text {
	font-size: 20px;
}
.checklist-form-info-item.rewards>.form-info-item>input {
	background-color: #912d2f;
}

.calculation-form-item>.form-info-item>:nth-child(2) {
	border-bottom: 1px solid #ccc;
	font-weight: 300;
	font-style: italic;
	min-height: 100%;
}
.calculation-form-item>.form-info-item>input {
	width: 100%;
	background-color: #fafafa;
}
.calculation-form-item>.form-info-item>input:hover {
	background-color: #ececec;
}
.calculation-form-item>.form-info-item>input:focus {
	background-color: #ccc;
}
.calculation-form-item>.form-info-item>.dropdown-cont {
	border-bottom: 0px;
	padding: 0px;
}
.calculation-form-item>.form-info-item>.dropdown-cont>.dropdown-option.selected {
	padding: 0px;
	padding-left: 5px;
	background-color: #fafafa;
}
.calculation-form-item>.form-info-item>.dropdown-cont>.dropdown-option.selected:hover {
	background-color: #ccc;
}
.calculation-dependents>.form-info-item>div {
	border-bottom: 1px solid #ccc;
	padding: 10px;
	height: 100%;
}

.calculation-minimize-button {
	height: 30px;
	width: 30px;
	font-size: 25px;
	margin-left: 15px;
	background-color: transparent;
	color: #c94a4c;
	filter: unset;
	font-weight: 600;
}
.calculation-minimize-button:hover {
	background-color: transparent;
	color: #912d2f;
}
.calculation-dependents {
	display: flex;
	flex-direction: column;
	gap: 10px;
	padding: 5px;
	margin-top: 10px;
	margin-bottom: 10px;
}

.calculation-dependents>.form-info-item {
	display: grid;
	grid-template-columns: 40% 60%;
	gap: 0px;
	align-items: center;
	font-weight:lighter;
	height: 40px;
}
.calculation-dependents>.form-info-item>.form-info-title {
	font-size: 20px;
	text-align: left;
}
.calculation-dependents>.form-info-item>input,
.checklist-form-info-item>.form-info-item>input{
	width: 100%;
	padding: 10px;
}
.calculation-dependents>.form-info-item>input {
	background-color: #fafafa;
}
.calculation-dependents>.form-info-item>input:hover {
	background-color: #ececec;
}
.calculation-dependents>.form-info-item>input:focus {
	background-color: #ccc;
}
.checklist-form-info-item>.form-info-item>.dropdown-cont>.dropdown-option.selected {
	padding: 1px;
	padding-left: 5px;
}
.calculation-dependents>.form-info-item>.dropdown-cont {
	padding: 0px;
	border-bottom: 0px;
}
.calculation-dependents>.form-info-item>.dropdown-cont>.dropdown-option.selected {
	padding: 10px;
}
.checklist-form-info-item>.form-info-item>.dropdown-cont>.dropdown-list {
	/*bottom: 40px;
	border-radius: 0px;
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;*/
}
.cl-child-form-item {
	width: 87%;
	margin-left: 10%;
	margin-right: 10%;
	grid-template-columns: 90% 8% !important;
}
.cl-child-form-item>.dropdown-cont>.dropdown-list {
	bottom: 30px;
	border-top-left-radius: 5px;
	border-top-right-radius: 5px;
	border-bottom: 0px;
	border-bottom-left-radius: 0px;
	border-bottom-right-radius: 0px;
}
.cl-child-form-item>.form-info-title {
	font-size: 22.5px !important;
	font-style: italic;
	color: #383838;
}
.cl-child-form-item.color-select {
	grid-template-columns: 60% 40% !important;
}

/*Surface Go: 200% scaling, 100% browser zoom*/
@media screen and (min-width: 924px) and (max-width: 1024px) {
	.checklists-list#list>.card>.card-content>.card-content {
		display: flex;
		flex-direction: column;
		height: fit-content;
		overflow: visible;
		padding-top: 0px;
		padding-bottom: 0px;
	}
	.checklists-list#list>.card>.card-content>.card-content>:nth-child(2n + 1) {
		padding-right: 0;
		border-right: 0px solid #ccc;
	}
}

/*Portrait Mode*/
@media screen and (orientation:portrait) {
	.checklist-form-info-item.rewards>.form-info-item>.form-info-title>.warning>.warning-text {
		font-size: 17px;
	}
}