/*Checklist creator form*/
#add-new-system {
	background-color: #b46e6e;
}
#add-new-system:hover {
	background-color: #6e2828;
}
#edit-system {
	width: 30px;
	background-color: transparent;
	color: #c94a4c;
	filter: unset;
	font-weight: 650;
}
#edit-system:hover {
	background-color: transparent;
	color: #912d2f;
}

/*Checklist Creator*/
#checklist-creator-card {
	min-height: fit-content;
	max-height: 93vh;
	min-width: 90vw;
	max-width: 90vw;
}
#checklist-creator-card>.card-content {
	height: calc(100% - 40px);
	overflow-y: scroll;
}
#checklist-creator-card>.card-menubar {
	position: relative;
	background-color: #5c1d1d;
}
#checklist-creator-card>.card-menubar>.card-actions>.close-button {
	background-color: #411212
}
#checklist-creator-card>.card-menubar>.card-actions>.close-button:hover {
	background-color: #c21919;
}
/*#checklist-creator-card>.card-content>.card-content {
	min-height: 88%;
	max-height: 88%;
	overflow-y: overlay;
}*/
#checklist-customizationations {
	display: grid;
	grid-template-columns: 42.5% 52.5%;
	gap: 5%;
}
#cl-creator-form-si, #cl-creator-form-cl {
	display: grid;
}
#cl-creator-form-si {
	height: fit-content;
}
#cl-creator-form-si,
#cl-creator-form-si>.form-info-item {
	gap: 10px;
}
#cl-creator-form-cl>.form-info-item {
	display: grid;
	grid-template-columns: 90% 10%;
}
#cl-creator-form-si>.form-info-item {
	display: grid;
	grid-template-columns: 10% 90%;
}
#cl-creator-form-cl>.form-info-item>:nth-child(2),
#cl-creator-form-si>.form-info-item>:nth-child(2) {
	border: 1px solid #ccc !important;
	background-color: #f1f1f1;
	height: 30px !important;
	max-width: unset;
	width: 30px !important;
}
#cl-creator-form-si>.form-info-item>:nth-child(2) {
	order: -1;
}
#cl-creator-form-cl>.cl-child-form-item {
	display: grid;
	grid-template-columns:30% 10% !important;
	justify-content: start;
	gap: 10px;
}
#cl-creator-sysname-cont>input {
	background-color: #fafafa;
}
#cl-creator-sysname-cont>input:hover {
	background-color: #ececec;
}
#cl-creator-sysname-cont>input:focus {
	outline: none;
    background-color: #ccc !important;
    color: rgb(25,25,25);
}
/*#checklist-creator-card>.card-content>.action-row {
	min-height: 11%;
	max-height: 11%;
}*/
/*#checklist-creator-card>.card-content>.action-row>.action-button {
	margin-bottom: 50px;
}*/
#cl-creator-sysname-cont>div {
	height: 100%;
	font-size: 30px;
	font-style: italic;
	text-align: right;
	line-height: 25px;
}
#cl-creator-scored-cont {
	display: grid;
	grid-template-columns: 70% 30%;
}
#cl-creator-scored-cont>div {
	align-self: end;
	justify-self: end;
}
#checklist-creator-add-button {
	background-color: #0b4e8a;
}
#checklist-creator-add-button:hover {
	background-color: #07355e
}

/*Portrait Mode*/
@media screen and (orientation:portrait) {
	#checklist-customizationations {
		display: flex;
		flex-direction: column;
	}
	#cl-creator-form-si>.form-info-item {
		grid-template-columns: 90% 10%;
	}
	#cl-creator-form-si>.form-info-item>:nth-child(2) {
		order: 1;
	}
}