/*Animations*/
@keyframes textshine {
    0%   {color: rgb(255, 63, 63);}
    25%  {color: rgb(47, 162, 255);}
    50%  {color: rgb(255, 63, 63);}
    100% {color: rgb(47, 162, 255);}
}
/*SIGNATURE CARD*/
#signature-pad {
	background-color: #ccc;
	border-radius: 10px;
	margin: 10px;
}
#signature-card {
	max-height: min-content;
	min-height: min-content;
}
#signature-card>.card-content {
	display: flex;
	flex-direction: column;
	gap: 10px;
	touch-action: none;
	padding: 0px;
}
#signature-button {
	position: absolute;
	top: 91%;
	left: 95%;
	height: 50px;
	width: 50px;
	border-radius: 30px;
	display: grid;
	align-items:start;
	justify-content: center;
}
#signature-button>img {
	height: 25px;
	width: 25px;
	align-self: center;
}
.appr-row {
	display: grid;
	grid-template-columns: 47.5% 26.25% 26.25%;
	align-items: center;
	justify-content: center;
	padding: 10px;
}
.appr-row>.action-button {
	width: 80%;
}
#signature-card>.card-content>.card {
	border: 0px;
}
.invoice-total-row#inv-labels {
	font-size: 30px;
}
.approve-button {
	background-color: #6ba43a;
}
.approve-button:hover {
	background-color: #84bd52;
}
#signature-card-card-content>.invoice-total-cont>.invoice-total-row {
	grid-template-columns: 40% 30% 30%;
}
#signature-card-card-content>.invoice-total-cont>.invoice-total-row>div {
	background-color: transparent;/*#f0f0f0*/;
	font-weight: 600;
	color: #000;/*rgb(54, 145, 248)*/;
	/*animation-name: textshine;
    animation-duration: 8s;
    animation-iteration-count: infinite;
    animation-direction: alternate-reverse;*/
}
#clear-canvas {
	margin: 10px;
}

/*Confirm re-sign Card*/
#confirmResignCard {
	min-height: fit-content;
}

#confirmResignCard>.card-content>.h4 {
	font-size: 25px;
}

#confirmResignCard>.card-content>.h5 {
	font-size: 30px;
	font-weight: 600;
}
#confirmResignCard>.card-content>.action-row {
	padding-top: 10px;
}

/*Surface Go: 200% scaling, 100% browser zoom*/
@media screen and (min-width: 924px) and (max-width: 1024px) {
	#signature-card {
		height: 90vh;
	}
	#signature-button {
		position: absolute;
		top: 89%;
		left: 95%;
		height: 35px;
		width: 35px;
		border-radius: 30px;
		display: grid;
		align-items:start;
		justify-content: center;
	}
	#signature-button>img {
		height: 20px;
		width: 20px;
	}
}

/*Portrait Mode*/
@media screen and (orientation:portrait) {
	#signature-button {
		left: 90%;
	}
}