.wo-preview-card {
	min-height: 37.5%;
	height: 37.5%;
	width: 100%;
	background-color: #fafafa;
	border: 1px solid #ccc;
	overflow: hidden;
}
.wo-preview-card>.card-content {
	height: 100%;
	padding: 0px;
}
.wo-preview-card>.card-content>.card-content {
	height: 80%;
	max-height: 80%;
	border-bottom: 1px solid #ccc;
}
.wo-preview-card>.card-content>.action-row {
	height: 20%;
	align-items: center;
	max-height: 20%;
	background-color: #f0f0f0;
}
.wo-preview-card>.card-content>.action-row>.action-button {
	width: 40%;
}
.wo-prev-info-cont>.form-info-item {
}
.wo-prev-date {
	font-weight: 700;
	font-size: 27.5px;
}
.wo-prev-tech {
	color: #212121;
	font-style: italic;
	font-size: 22.5px;
}
.wo-prev-cust {
	color: #212121;
	font-style: italic;
	font-size: 17.5px;
	font-weight: 300;
}

.wo-descr {
	max-height: 50%;
	color: #272727;
	text-align: left;
	overflow-y: auto;
}